import axios from "axios";

export const handleLogout = (logOut, navigate) => { // logOut   login  
  axios.post('/user/logout', {}, { withCredentials: true })
      .then(res => {
          logOut();
          navigate('/');
      })
      .catch(error => console.error(error));
};

export const user_logout = (logOut) => { // logOut   login  
    axios.post('/user/logout', {}, { withCredentials: true })
        .then(res => {
            logOut();
        })
        .catch(error => console.error(error));
  };
  