import NavigationBar from './nav/Nav';
import Sidebar from './sidebar/sidebar';
import './Layout.css'

// Layout     
//   NavigationBar Sidebar 
//         .
// children prop     ,
//      .

const Layout = ({ children }) => {
  return (
    <>
      <NavigationBar />
      <Sidebar />
      <div className="layout-content">{children}</div>
    </>
  );
};

export default Layout;